.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 20px;
  background-color: rgb(235, 234, 233);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle1,
.p-circle2,
.p-circle3 {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
}

.p-circle1 {
  background-color: rgb(252, 96, 91);
}

.p-circle2 {
  background-color: rgb(252, 187, 64);
}

.p-circle3 {
  background-color: rgb(51, 198, 72);
}

.p-img {
  width: 100%;
  transition: all 10s ease;
}

.p:hover .p-img {
  transform: translateY(-100%);
}

@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 20vh;
  }
}